import React from 'react'

import { Container, FormLabel, FormWrap, Icon, FormContent, Form, FormH1, FormInput, FormButton, Text } from './SigninElements'

const SignIn = () => {


    return (
        <>
            <Container>
                <FormWrap>
                    <FormContent>
                        <Form action="#">
                        <Icon to="/">Fridge Friend</Icon>
                            <FormH1>Sign in to your account</FormH1>
                            <FormLabel htmlFor='for'>Email</FormLabel>
                                <FormInput type='email' required />
                            <FormLabel htmlFor='for'>Password</FormLabel>
                                <FormInput type='password' required />
                            <FormButton to="/">Continue</FormButton>
                            <Text>This feature isn't available yet! Please click the Fridge Friend logo at the top of the box, or the Continue button, to return to the main screen.</Text>
                        </Form>
                    </FormContent>
                </FormWrap>
            </Container> 
        </>
    )
}

export default SignIn
